































































































































































import { Vue, Component } from 'vue-property-decorator';
import { dataViewDetail } from '@api/viewData';

@Component
export default class DetailData extends Vue {
  dataList: any = {};
  pageDetail = '';
  // defaultProps: any = {
  //   label: "fieldCode",
  //   value: "fieldCode"
  // };
  dictFlag = false;
  dataTtype = '';
  typeDetail = '';
  actions() {
    const _this = this;
    return [
      {
        icon: '',
        onClick: () => {
          _this.$router.back();
        },
        type: 'button',
        title: '返回'
      }
    ];
  }
  historyList: Array<any> = [
    {
      prop: 'version',
      label: '历史版本列表'
    },
    {
      prop: 'status',
      label: '',
      align: 'center',
      render(h: any, { record }: any) {
        let statusText = '';
        if (record.status === '0') {
          statusText = '启用';
        } else if (record.status === '1') {
          statusText = '未启用';
        } else if (record.status === '2') {
          statusText = '已过期';
        } else if (record.status === '3') {
          statusText = '已停用';
        }
        return h('span', {}, statusText);
      }
    }
  ];
  statusData: Array<any> = [];
  statusList: Array<any> = [
    {
      prop: 'code',
      label: '状态码',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'desc',
      label: '描述',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'isNormal',
      label: '是否异常',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    }
  ];
  dataListTree: Array<any> = [];
  historyListData: Array<any> = [];
  paramLoading = false;
  paramData: Array<any> = [];
  paramList: Array<any> = [
    {
      prop: '',
      type: 'index',
      label: '序号',
      align: 'center',
      showOverflowTooltip: true,
      width: '70'
    },
    {
      prop: 'code',
      label: '参数code',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'name',
      label: '参数名称',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'type',
      label: '参数类型',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100',
      render(h: any, { record }: any) {
        let typeText = '';
        if (record.type === 0) {
          typeText = '字符串';
        } else if (record.type === 1) {
          typeText = '数字';
        } else if (record.type === 2) {
          typeText = '日期';
        } else if (record.type === 3) {
          typeText = '布尔';
        }
        return h('span', {}, typeText);
      }
    },
    // {
    //   prop: "transType",
    //   label: "传递方式",
    //   align: "center",
    //   showOverflowTooltip: true,
    //   "min-width": "100",
    //   render(h, { record }) {
    //     let transTypeText = "";
    //     if (record.transType === 0) {
    //       transTypeText = "路径参数";
    //     } else if (record.transType === 2) {
    //       transTypeText = "formData";
    //     }
    //     return h("span", {}, transTypeText);
    //   }
    // },
    {
      prop: 'required',
      label: '是否必传',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100',
      render(h: any, { record }: any) {
        let requiredText = '';
        if (record.required === 0) {
          requiredText = '是';
        } else if (record.required === 1) {
          requiredText = '否';
        } else if (record.required === 3) {
          requiredText = '固定值';
        }
        return h('span', {}, requiredText);
      }
    },
    {
      prop: 'defaultValue',
      label: '默认值',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    }
  ];
  paramListService: Array<any> = [
    {
      prop: '',
      type: 'index',
      label: '序号',
      align: 'center',
      showOverflowTooltip: true,
      width: '70'
    },
    {
      prop: 'code',
      label: '参数code',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'name',
      label: '参数名称',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'type',
      label: '参数类型',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100',
      render(h: any, { record }: any) {
        let typeText = '';
        if (record.type === 0) {
          typeText = '字符串';
        } else if (record.type === 1) {
          typeText = '数字';
        } else if (record.type === 2) {
          typeText = '日期';
        } else if (record.type === 3) {
          typeText = '布尔';
        }
        return h('span', {}, typeText);
      }
    },
    {
      prop: 'transType',
      label: '传递方式',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100',
      render(h: any, { record }: any) {
        let transTypeText = '';
        if (record.transType === 0) {
          transTypeText = '路径参数';
        } else if (record.transType === 2) {
          transTypeText = 'formData';
        }
        return h('span', {}, transTypeText);
      }
    },
    {
      prop: 'required',
      label: '是否必传',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100',
      render(h: any, { record }: any) {
        let requiredText = '';
        if (record.required === 0) {
          requiredText = '是';
        } else if (record.required === 1) {
          requiredText = '否';
        } else if (record.required === 3) {
          requiredText = '固定值';
        }
        return h('span', {}, requiredText);
      }
    },
    {
      prop: 'defaultValue',
      label: '默认值',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    }
  ];
  fieldData: Array<any> = [];
  fieldLoading = false;
  fieldList: Array<any> = [
    {
      prop: 'fieldCode',
      label: '字段code',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'fieldName',
      label: '字段名称',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'foreignFieldName',
      label: '关联字段',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '140'
    }
  ];
  fieldListData: Array<any> = [
    {
      prop: 'fieldCode',
      label: '字段code',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'fieldName',
      label: '字段名称',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'foreignFieldName',
      label: '关联字段',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '140'
    },
    {
      prop: 'isPrimaryKey',
      label: '是否主键',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100',
      render(h: any, { $index, record }: any) {
        return h('el-switch', {
          props: {
            disabled: true,
            value: record.isPrimaryKey,
            'active-value': 0,
            'inactive-value': 1,
            'active-text': '是',
            'inactive-text': '否'
          }
        });
      }
    }
  ];
  viewListData: Array<any> = [];
  viewLoading = false;
  viewList: Array<any> = [
    {
      prop: 'quote',
      label: '引用方类型',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'quoteDesc',
      label: '引用方描述',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
    },
    {
      prop: 'lastUsed',
      label: '最近一次调用时间',
      align: 'center',
      showOverflowTooltip: true,
      'min-width': '100'
      // },
      // {
      //   prop: "",
      //   label: "最近一次调用次数",
      //   align: "center",
      //   showOverflowTooltip: true,
      //   "min-width": "100"
    }
  ];
  handleCurrentChange(val: any) {
    this.detailView(val.id);
  }
  created() {
    const id: any = this.$route.query.id;
    this.detailView(id);
  }
  detailView(id: any) {
    dataViewDetail(id).then((data) => {
      if (data.error) {
        this.$message.error(data.data.error);
      } else {
        this.dataList = data.data;
        this.dataTtype = this.dataList.type.toString();
        this.typeDetail = this.dataList.kind.toString();
        this.pageDetail = this.dataList.type.toString();
        if (this.dataList.dictFlag === 0) {
          this.dictFlag = true;
        } else {
          this.dictFlag = false;
        }
        this.paramData = this.dataList.queryParams;
        this.dataList.fields.forEach((field) => {
          if (field.foreignFieldName) {
            field.foreignFieldName = `${field.foreignName}.${field.foreignFieldName}`;
          }
        });
        this.dataListTree = this.dataList.fields;
        this.fieldData = this.dataList.fields;
        this.historyListData = this.dataList.historys;
        this.viewListData = this.dataList.viewUseds;
        this.statusData = this.dataList.respCodes;
      }
    });
  }
}
